* {
    margin: auto;
    box-sizing: border-box;
  }
  img {
    width: 50px;
  }
  h1 {
    text-align: center;
  
  }
  h2 {
    text-align: center;
  
  }
  .card {
    border-radius: 25px;
    border: 2px solid #49494a;
    padding: 20px;
    width: 150px;
    margin: 20px;
    font-size: .8em;
  
  }
  
  
  
  /* navbar */
  .navbar {
    height: 100%;
    width: 1600px;
    position: row;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
  
  .navbar a {
    padding: 6px 8px 6px 16px;
    text-decoration: none;
    font-size: 25px;
    color: #818181;
    display: block;
  }
  
  .navbar a:hover {
    color: #f1f1f1;
  }
  
  .main {
    margin-left: 100px; /* Same as the width of the sidenav */
    font-size: 28px; /* Increased text to enable scrolling */
    padding: 0px 10px;
    display: flex;
    flex-wrap: wrap;
    flex: 0 0 33.333333%;
  
  }
  /* form css */
  form {
    width: 50%;
    margin-top: 30%;
  
  }
  form button[type=submit]:hover {
    background-color: #7bd853;
  }
  form div {
    border-radius: 5px;
    background-color: #d9dadc;
    padding: 20px;
  }
  form input[type=text], select {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
    display: inline-block;
    border: 1px solid rgb(61, 58, 58);
    border-radius: 4px;
    box-sizing: border-box;
  }
  
  form button[type=submit] {
    display: block;
    width: 25%;
    background-color: #52a82d;
    color: white;
    padding: 14px 20px;
    margin: 8px 0;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }